.container-fuild:nth-child(11)
  background: linear-gradient(to bottom left, $home_linear_gradient3, $bg-color2)
  .container
    min-height: 53vh
    padding-top: 3.5em
    padding-bottom: 3.5em

  .row
    .left_column,.right_column
      box-sizing: border-box
      min-height: 480px
      max-height: 100%

    .left_column
      background: linear-gradient(to left bottom, $bg-color1, $bg-color3)
      .left_column_container
        padding-top: 6.5em
        padding-bottom: 3em

      h1
        font-weight: 700
        font-family: Roboto, sans-serif
        margin-left: 1em
      h5
        font-weight: 700
        font-family: Roboto, sans-serif
        font-size: 1.2em
        margin: 2.7em auto 2.7em 2em
      button.btn.btn-outline-dark
        margin-left: 2.5em

    .right_column

      .fotograf
        min-height: 50%
        max-height: 50%
        background-image: url(../img/fotocreative.jpg)
        background-repeat: no-repeat
        background-size: cover
        background-position: 100%  21.39%
      .ilustaration
        display: flex
        flex-direction: column
        min-height: 50%
        max-height: 50%
        .row:nth-child(1)
          min-height: 128px
          max-height: 128px

          div:nth-child(1)
            min-height: 50%
            max-height: 100%
            background-image: url(../img/fotocreative2.jpg)
            background-repeat: no-repeat
            background-size: cover
            background-position: 50%  50%
          div:nth-child(2)
            min-height: 50%
            max-height: 100%
            background-image: url(../img/fotocreative3.jpg)
            background-repeat: no-repeat
            background-size: cover
            background-position: 50%  50%
        .row:nth-child(2)
          min-height: 128px
          max-height: 128px

          div:nth-child(1)
            // min-height: 157px
            max-height: 100%
            background-image: url(../img/fotocreative4.jpg)
            background-repeat: no-repeat
            background-size: cover
            background-position: 50%  50%
          div:nth-child(2)
            // min-height: 157px
            max-height: 100%
            background-image: url(../img/fotocreative5.jpg)
            background-repeat: no-repeat
            background-size: cover
            background-position: 50%  50%
