.container-fuild:nth-child(9) {
  background: linear-gradient(to bottom left, #db08f7, #081cf7);
}
.container-fuild:nth-child(9) .container-lg {
  min-height: 53vh;
  padding-top: 3.5em;
  padding-bottom: 3.5em;
}
.container-fuild:nth-child(9) .wrapper {
  width: 100%;
  text-align: center;
}
.container-fuild:nth-child(9) h1 {
  font-family: Roboto, sans-serif, Arial;
  font-weight: 700;
  color: black;
  margin-bottom: 40px;
}
.container-fuild:nth-child(9) .col-12 {
  padding: 0;
}
.container-fuild:nth-child(9) .carusel {
  width: 100%;
  height: 500px;
  margin: 0 auto;
  overflow: hidden;
}
.container-fuild:nth-child(9) .carusel-line {
  display: flex;
  position: relative;
  left: 0;
  transition: 0.8s;
}
.container-fuild:nth-child(9) .carusel-line-item {
  display: inline-block;
  height: 500px;
  width: 100%;
}
.container-fuild:nth-child(9) .carusel-line-item:nth-child(1) {
  background-image: url("../img/restoran.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.container-fuild:nth-child(9) .carusel-line-item:nth-child(2) {
  background-image: url("../img/companyhome.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.container-fuild:nth-child(9) .carusel-line-item:nth-child(3) {
  background-image: url("../img/cars.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.container-fuild:nth-child(9) a {
  color: white;
}
.container-fuild:nth-child(9) .arrow {
  position: absolute;
  top: 50%;
  width: 4vmin;
  height: 4vmin;
  background: transparent;
  border-top: 1vmin solid white;
  border-right: 1vmin solid white;
  box-shadow: 0 0 0 lightgray;
  transition: all 200ms ease;
  z-index: 100;
}
.container-fuild:nth-child(9) .arrow.left {
  left: 25px;
  transform: translate3d(0, -50%, 0) rotate(-135deg);
}
.container-fuild:nth-child(9) .arrow.right {
  right: 25px;
  transform: translate3d(0, -50%, 0) rotate(45deg);
}
.container-fuild:nth-child(9) .arrow:hover {
  border-color: orange;
  box-shadow: 0.5vmin -0.5vmin 0 white;
}
.container-fuild:nth-child(9) .arrow::before {
  content: "";
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-40%, -60%) rotate(45deg);
  width: 300%;
  height: 300%;
}